import { DrawLayerData } from "./DrawLayerData";
export class PixcelArtData {
    //=============================================
    // constructor
    //=============================================
    constructor() {
        //=============================================
        // private
        //=============================================
        //=============================================
        // public
        //=============================================
        this.getJson = () => {
            let obj = {};
            for (var key in this._drawLayerDataList) {
                let layerData = this._drawLayerDataList[key];
                obj[key] = layerData.getJson();
            }
            let result = { "id": this._id, "title": this._title, "dot_json": obj };
            return result;
        };
        this.setDrawLayerData = (name, layerData) => {
            //console.log("setLayerData", name, layerData);
            this._drawLayerDataList[name] = layerData;
            //this.layoutDataInit();
        };
        this.getDrawLayerDataList = () => {
            return this._drawLayerDataList;
        };
        this.layoutInit = () => {
            for (var key in this._dot_json) {
                let dot_json = this._dot_json[key];
                let layerData = new DrawLayerData();
                layerData.setJson(dot_json);
                this._drawLayerDataList[key] = layerData;
                //console.log(key);
            }
            let minX = Number.MAX_SAFE_INTEGER;
            let minY = Number.MAX_SAFE_INTEGER;
            let maxX = 0;
            let maxY = 0;
            //console.log(this._drawLayerDataList);
            for (var key in this._drawLayerDataList) {
                //console.log(key);
                let ld = this._drawLayerDataList[key];
                //console.log(key, ld.x, ld.y, ld.width, ld.height);
                if (ld.x < minX) {
                    minX = ld.x;
                }
                if (ld.y < minY) {
                    minY = ld.y;
                }
                if (maxX < ld.x + ld.width) {
                    maxX = ld.x + ld.width;
                }
                if (maxY < ld.y + ld.height) {
                    maxY = ld.y + ld.height;
                }
            }
            //console.log("result", minX, minY, maxX, maxY);
            this._x = minX;
            this._y = minY;
            this._width = maxX - minX;
            this._height = maxY - minY;
            //console.log(this._x, this._y, this._width, this._height);
        };
        this._drawLayerDataList = {};
    }
    //=============================================
    // getter/setter
    //=============================================
    get id() {
        return this._id;
    }
    get title() {
        return this._title;
    }
    get x() {
        return this._x;
    }
    set x(value) {
        this._x = value;
    }
    get y() {
        return this._y;
    }
    set y(value) {
        this._y = value;
    }
    get width() {
        return this._width;
    }
    get height() {
        return this._height;
    }
}
