import { State } from "../../model/State";
export class FileDropdownMenu extends createjs.EventDispatcher {
    //----------protected-------
    //=============================================
    // constructor
    //=============================================
    constructor(state) {
        super();
        //=============================================
        // /
        //=============================================
        //----------public----------
        this.EVENT_DROPDOWN_OPEN = "event dropdown open";
        this.EVENT_DROPDOWN_CLOSE = "event dropdown close";
        this.EVENT_DROPDOWN_MENU_SELECT = "event dropdown menu select";
        //=============================================
        // event handler
        //=============================================
        //=============================================
        // private
        //=============================================
        this._reset = () => {
        };
        this._state = state;
        //----------------------
        // 
        //----------------------
        let fdd = document.querySelector('#file-dropdown');
        //
        fdd.addEventListener("show.bs.dropdown", (e) => {
            this.dispatchEvent(new createjs.Event(this.EVENT_DROPDOWN_OPEN, true, true));
        });
        //
        fdd.addEventListener("hide.bs.dropdown", (e) => {
            this._reset();
            this.dispatchEvent(new createjs.Event(this.EVENT_DROPDOWN_CLOSE, true, true));
        });
        //----------------------
        // 
        //----------------------
        //JSON
        this._loadJsonFileFlomLocalLink = document.querySelector('#file-dropdown #loadJsonFileFromLocal');
        this._loadJsonFileFlomLocalLink.addEventListener("click", (e) => {
            this._state.current = State.FILE_LOAD_JSON_FROM_LOCAL;
            this.dispatchEvent(new createjs.Event(this.EVENT_DROPDOWN_MENU_SELECT, true, true));
            e.preventDefault();
        });
        //----------------------
        // 
        //----------------------
        //JSON
        this._saveJsonFileToLocalLink = document.querySelector('#file-dropdown #saveJsonFileToLocal');
        this._saveJsonFileToLocalLink.addEventListener("click", (e) => {
            this._state.current = State.FILE_SAVE_JSON_TO_LOCAL;
            this.dispatchEvent(new createjs.Event(this.EVENT_DROPDOWN_MENU_SELECT, true, true));
            e.preventDefault();
        });
    }
}
