export class DrawLayerData {
    //=============================================
    // constructor
    //=============================================
    constructor() {
        //=============================================
        // private
        //=============================================
        //=============================================
        // public
        //=============================================
        this.setData = (width, height, colorList, dataList, x = 0, y = 0) => {
            this._width = width;
            this._height = height;
            this._colorList = colorList;
            this._dataList = dataList;
            this._x = x;
            this._y = y;
        };
        this.setJson = (obj) => {
            //console.log("setJson", obj);
            let size = obj.size;
            this._width = size[0];
            this._height = size[1];
            this._colorList = obj.color;
            this._dataList = obj.data;
            if (obj.pos) {
                let pos = obj.pos;
                this._x = pos[0];
                this._y = pos[1];
            }
            else {
                this._x = 0;
                this._y = 0;
            }
        };
        this.getJson = () => {
            let result = { "size": [this._width, this._height], "color": this._colorList, "data": this._dataList };
            if (this._x != 0 || this._y != 0) {
                result["pos"] = [this._x, this._y];
            }
            return result;
        };
        this._colorList = [];
        this._dataList = [];
    }
    //=============================================
    // getter/setter
    //=============================================
    /*
    get name(): string {
        return this._name;
    }
    set name(value: string) {
        this._name = value;
    }
    */
    get x() {
        return this._x;
    }
    set x(value) {
        this._x = value;
    }
    get y() {
        return this._y;
    }
    set y(value) {
        this._y = value;
    }
    get width() {
        return this._width;
    }
    get height() {
        return this._height;
    }
    get colorList() {
        return this._colorList;
    }
    get dataList() {
        return this._dataList;
    }
}
